* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Arial, sans-serif;
}

body {
  color: #fff;
  background-color: #0b0c10;
  width: 100vw;
  margin: 0;
  padding: 0;
  font-family: Poppins, sans-serif;
}

.navbar {
  backdrop-filter: blur(10px);
  z-index: 1000;
  background: #191919cc;
  border-radius: 10px;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  margin: 20px auto;
  padding: 15px 40px;
  display: flex;
  position: sticky;
  top: 0;
  box-shadow: 0 4px 10px #ffffff1a;
}

.logo {
  color: #ff6b6b;
  font-size: 24px;
  font-weight: bold;
}

.nav-links {
  gap: 20px;
  list-style: none;
  display: flex;
}

.nav-links li a {
  color: #fff;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s;
}

.nav-links li a:hover {
  color: #ff4757;
}

.cta-button {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(135deg, #ff4757, #ff6b6b);
  border: none;
  border-radius: 20px;
  padding: 10px 15px;
  font-size: 16px;
  transition: all .3s;
}

.cta-button:hover {
  background: linear-gradient(135deg, #ff6b6b, #e84118);
  transform: scale(1.1);
}

.hero {
  text-align: center;
  background: url("https://wallpapercave.com/wp/wp9461461.jpg") center / cover no-repeat;
  justify-content: center;
  align-items: center;
  height: 70vh;
  display: flex;
}

.hero-content h1 {
  -webkit-text-fill-color: transparent;
  background: linear-gradient(135deg, #ff6b6b, #e84118);
  -webkit-background-clip: text;
  font-size: 42px;
}

.hero-content p {
  margin-top: 10px;
  font-size: 18px;
}

.hero-btn {
  color: #fff;
  cursor: pointer;
  background: #ff4757;
  border: none;
  border-radius: 25px;
  margin-top: 20px;
  padding: 12px 25px;
  font-size: 18px;
  transition: all .3s;
}

.hero-btn:hover {
  background: #e84118;
  transform: scale(1.1);
}

.trending-movies {
  text-align: center;
  margin: 50px auto;
}

.movie-container {
  justify-content: center;
  gap: 15px;
  margin-top: 20px;
  display: flex;
}

.movie-card {
  cursor: pointer;
  background: #1a1a2e;
  border-radius: 10px;
  padding: 15px 20px;
  transition: all .3s;
}

.movie-card:hover {
  background: #ff4757;
}

.categories {
  text-align: center;
  margin: 50px auto;
}

.genre-container {
  flex-wrap: wrap;
  justify-content: center;
  gap: 15px;
  margin-top: 20px;
  display: flex;
}

.genre-card {
  cursor: pointer;
  background: #162447;
  border-radius: 10px;
  padding: 15px 25px;
  transition: all .3s;
}

.genre-card:hover {
  background: #ff4757;
}

.footer {
  text-align: center;
  background: #0b0c10;
  margin-top: 50px;
  padding: 20px;
}

.Trending-movies-title {
  margin-left: 4rem;
  display: flex;
}

.movie-list {
  grid-template-columns: repeat(auto-fit, minmax(350px, 1fr));
  justify-content: center;
  gap: 1rem;
  padding: 20px;
  display: grid;
}

.movie-card {
  background: #222;
  border-radius: 12px;
  flex-direction: column;
  max-width: 280px;
  margin: auto;
  transition: transform .3s, box-shadow .3s;
  display: flex;
  overflow: hidden;
  box-shadow: 0 6px 15px #0000004d;
}

.movie-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 20px #00000080;
}

.movie-poster {
  object-fit: cover;
  border-bottom: 3px solid #ff9800;
  width: 100%;
  height: 200px;
}

.movie-content {
  text-align: center;
  padding: 15px;
}

.movie-title {
  color: #fff;
  white-space: nowrap;
  text-overflow: ellipsis;
  margin-bottom: 5px;
  font-size: 1.4rem;
  font-weight: bold;
  overflow: hidden;
}

.movie-genre {
  color: #ff9800;
  margin-bottom: 5px;
  font-size: .9rem;
  font-weight: 500;
}

.movie-description {
  color: #ddd;
  text-overflow: ellipsis;
  -webkit-line-clamp: 2;
  -webkit-box-orient: vertical;
  max-height: 45px;
  margin-bottom: 10px;
  font-size: .85rem;
  line-height: 1.4;
  display: -webkit-box;
  overflow: hidden;
}

.movie-btn {
  color: #222;
  cursor: pointer;
  text-transform: uppercase;
  background: #ff9800;
  border: none;
  border-radius: 6px;
  width: 100%;
  padding: 10px 15px;
  font-size: .9rem;
  font-weight: bold;
  transition: background .3s ease-in-out, transform .2s;
}

.movie-btn:hover {
  background: #e68900;
}

.movie-btn:active {
  transform: scale(.95);
}

.search-bar {
  background: #1e1e1e;
  border-radius: 50px;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 600px;
  margin: 20px auto;
  padding: 10px;
  display: flex;
  box-shadow: 0 4px 10px #ffffff1a;
}

.search-bar input {
  color: #fff;
  background: #2c2c2c;
  border: none;
  border-radius: 50px;
  outline: none;
  flex: 1;
  padding: 12px 20px;
  font-size: 16px;
  transition: all .3s;
}

.search-bar input::placeholder {
  color: #fff9;
}

.search-bar input:focus {
  background: #333;
  box-shadow: 0 0 8px #fff3;
}

.search-bar button {
  color: #fff;
  cursor: pointer;
  background: linear-gradient(135deg, #ff6b6b, #ff4757);
  border: none;
  border-radius: 50px;
  margin-left: 10px;
  padding: 12px 20px;
  font-size: 16px;
  transition: all .3s;
}

.search-bar button:hover {
  background: linear-gradient(135deg, #ff4757, #e84118);
  transform: scale(1.05);
}

.loading-effect {
  opacity: .5;
  pointer-events: none;
  filter: blur(2px);
  transition: opacity .3s ease-in-out, filter .3s ease-in-out;
}

.error-message {
  color: #ff4757;
  text-align: center;
  margin-top: 10px;
  font-size: 18px;
  font-weight: bold;
}

@media (width <= 768px) {
  .movie-list {
    grid-template-columns: repeat(auto-fit, minmax(220px, 1fr));
  }

  .movie-card {
    max-width: 100%;
  }

  .movie-poster {
    height: 300px;
  }
}
